.Dropzone {
    height: 200px;
    width: 100%;
    background-color: #fff;
    border: 2px dashed rgb(187, 186, 186);
    /* border-radius: 50%; */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    font-size: 16px;
  }
  
  .Highlight {
    background-color: rgb(188, 185, 236);
  }
  
  .Icon {
    opacity: 0.3;
    height: 64px;
    width: 64px;
  }
  
  .FileInput {
    display: none;
  }


